'use client';

import { useDAWStore } from '@/store/useDAWStore';
import { modalClient } from '@/lib/modal-client';
import { Fader } from '@/components/daw/Fader';
import { Settings2, Activity } from 'lucide-react';

export default function Studio() {
    const faders = useDAWStore((state) => state.faders);
    const isTraining = useDAWStore((state) => state.isTraining);
    const setTrainingStatus = useDAWStore((state) => state.setTrainingStatus);
    const setFaderValue = useDAWStore((state) => state.setFaderValue);

    const handleAutoMix = async () => {
        setTrainingStatus(true);
        try {
            const response = await modalClient.process_vocal_cloning();

            if (response.success) {
                // Auto mix positions from Modal AI
                setFaderValue('vocal', response.mixParameters.vocalLevel);
                setFaderValue('beat', response.mixParameters.beatLevel);
                setFaderValue('bass', 65);
                setFaderValue('fx', 50);
            }
        } catch (error) {
            console.error("Auto Mix Modal training failed:", error);
        } finally {
            setTrainingStatus(false);
        }
    };

    return (
        <div className="flex flex-col h-full w-full items-center justify-between pointer-events-auto">

            {/* TOP HEADER: Branding */}
            <div className="absolute top-8 flex flex-col items-center justify-center opacity-80 mix-blend-screen pointer-events-none">
                <div className="border border-cyan-glow/30 px-6 py-2 rounded-sm bg-black/50 backdrop-blur-md shadow-[0_0_15px_rgba(0,240,255,0.1)]">
                    <h1 className="text-2xl font-sans tracking-widest text-[#E0E0E0]">
                        DA GRABA <span className="font-light">STUDIO CONSOLE</span>
                    </h1>
                </div>
                <p className="text-[#A0A0A0] text-xs tracking-wide mt-2">Soy ingeniero y productor musical.</p>
            </div>

            {/* The rest of the screen is purely the console */}

            {/* LOWER SECTION: Analog Mix Desk (Perspective Container) */}
            <div className="w-full mt-auto perspective-container flex flex-col items-center justify-end z-10 pb-0">

                {/* Floating Analysis Screens */}
                <div className="flex w-full max-w-6xl justify-between px-8 mb-4 transform translate-y-8 z-0">
                    {/* Left: ANALYSIS Waveform */}
                    <div className="glass-panel w-96 h-48 rounded-md border border-[#333] shadow-2xl flex flex-col bg-[#050B14]/90 p-4">
                        <div className="flex justify-between items-center text-[10px] text-silver-dark font-mono border-b border-[#222] pb-2 mb-2">
                            <span>ANALYSIS</span>
                            <div className="flex gap-2">
                                <span className="w-2 h-2 rounded-full bg-cyan-glow shadow-[0_0_5px_rgba(0,240,255,0.8)]" />
                                <span className="w-2 h-2 rounded-full bg-red-500 shadow-[0_0_5px_rgba(255,0,0,0.8)]" />
                            </div>
                        </div>
                        {/* Fake Waveform image replacement */}
                        <div className="flex-1 flex items-center justify-center relative overflow-hidden">
                            <div className="absolute inset-y-0 w-full flex items-center">
                                {/* Left/Cyan */}
                                <div className="h-24 w-1/2 flex items-center gap-[2px] opacity-80">
                                    {Array.from({ length: 40 }).map((_, i) => (
                                        <div key={`l-${i}`} className="w-[2px] bg-cyan-glow shadow-[0_0_8px_rgba(0,240,255,0.6)]" style={{ height: `${20 + 80 * Math.abs(Math.sin(i * 4.321))}%` }} />
                                    ))}
                                </div>
                                {/* Right/Red */}
                                <div className="h-24 w-1/2 flex items-center gap-[2px] opacity-80">
                                    {Array.from({ length: 40 }).map((_, i) => (
                                        <div key={`r-${i}`} className="w-[2px] bg-red-500 shadow-[0_0_8px_rgba(255,0,0,0.6)]" style={{ height: `${20 + 80 * Math.abs(Math.cos(i * 5.432))}%` }} />
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Right: ANALYSIS EQ */}
                    <div className="glass-panel w-96 h-48 rounded-md border border-[#333] shadow-2xl flex flex-col bg-[#050B14]/90 p-4">
                        <div className="flex justify-between items-center text-[10px] text-silver-dark font-mono border-b border-[#222] pb-2 mb-2">
                            <span>MASTER EQ</span>
                            <div className="flex gap-4 items-center">
                                <button
                                    onClick={handleAutoMix}
                                    disabled={isTraining}
                                    className="flex items-center gap-1 bg-cyan-glow/10 hover:bg-cyan-glow/20 border border-cyan-glow text-cyan-glow px-2 py-1 rounded shadow-[0_0_10px_rgba(0,240,255,0.2)] transition-colors disabled:opacity-50"
                                >
                                    <Activity size={10} />
                                    <span>AUTO-MEZCLA IA</span>
                                </button>
                                <Settings2 size={12} className="text-cyan-glow" />
                            </div>
                        </div>
                        {/* Fake Parametric EQ */}
                        <div className="flex-1 relative flex items-end pb-4 border-b border-l border-[#222]">
                            <svg className="absolute inset-0 w-full h-full" preserveAspectRatio="none">
                                <path d="M0,100 C50,10 100,120 150,50 C200,150 250,20 350,90 L350,150 L0,150 Z" fill="rgba(0,240,255,0.1)" />
                                <path d="M0,100 C50,10 100,120 150,50 C200,150 250,20 350,90" fill="none" stroke="#00F0FF" strokeWidth="2" strokeLinecap="round" className="drop-shadow-[0_0_5px_rgba(0,240,255,0.8)]" />
                            </svg>
                            {/* EQ Nodes */}
                            <div className="absolute left-[50px] top-[10px] w-3 h-3 rounded-full bg-cyan-glow shadow-[0_0_10px_rgba(0,240,255,0.8)] border border-white" />
                            <div className="absolute left-[150px] top-[50px] w-3 h-3 rounded-full bg-cyan-glow shadow-[0_0_10px_rgba(0,240,255,0.8)] border border-white" />
                        </div>
                    </div>
                </div>

                {/* The Analog Desk Hardware */}
                <div className="w-[120vw] bg-[#1a1a1a] console-desk border-t-4 border-[#333] shadow-2xl relative">
                    {/* Desk Grain Texture */}
                    <div className="absolute inset-0 bg-[url('/noise.png')] opacity-40 mix-blend-overlay pointer-events-none" />

                    <div className="max-w-7xl mx-auto flex justify-center py-8 px-12 relative z-10 gap-1 bg-[#0a0a0a] rounded-t-sm border border-[#222] shadow-[inset_0_10px_20px_rgba(0,0,0,0.8)]">

                        {/* Left Bank Filler Faders (Static hardware look) */}
                        {Array.from({ length: 6 }).map((_, i) => (
                            <div key={`fill-l-${i}`} className="w-16 h-full opacity-50 pointer-events-none pb-12">
                                <div className="h-[300px] bg-[#111] border-x border-[#222] w-full flex justify-center pt-4">
                                    <div className="w-1.5 h-full bg-black rounded-full" />
                                </div>
                            </div>
                        ))}

                        {/* Middle Active 4 Faders (The Zustand Store) */}
                        <div className="flex gap-2 mx-4 pb-4">
                            {faders.map((fader) => (
                                <Fader key={fader.id} id={fader.id} />
                            ))}
                        </div>

                        {/* Right Bank Filler Faders */}
                        {Array.from({ length: 6 }).map((_, i) => (
                            <div key={`fill-r-${i}`} className="w-16 h-full opacity-50 pointer-events-none pb-12">
                                <div className="h-[300px] bg-[#111] border-x border-[#222] w-full flex justify-center pt-4">
                                    <div className="w-1.5 h-full bg-black rounded-full" />
                                </div>
                            </div>
                        ))}

                    </div>
                </div>
            </div>

            {/* FULLSCREEN TRAINING / PROCESSING OVERLAY */}
            {isTraining && (
                <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-black/80 backdrop-blur-md pointer-events-auto">
                    {/* The spectacular glowing DG Logo */}
                    <div className="w-64 h-64 rounded-3xl relative flex items-center justify-center shadow-[0_0_100px_rgba(255,107,0,0.8)] animate-pulse overflow-hidden border border-orange-500/30">
                        {/* Outer spinner ring */}
                        <div className="absolute inset-0 border-8 border-t-white border-r-transparent border-b-white/10 border-l-transparent rounded-3xl animate-spin z-10" />
                        <img src="/logo.jpg" alt="DA GRABA Loading Logo" className="w-full h-full object-cover" />
                    </div>

                    <div className="mt-8 flex flex-col items-center gap-2">
                        <div className="flex items-center gap-3">
                            <Activity size={16} className="text-primary animate-bounce" />
                            <h2 className="text-xl font-bold tracking-widest text-[#E0E0E0]">PROCESANDO EN MODAL</h2>
                        </div>
                        <p className="text-silver-dark text-sm font-mono tracking-wide max-w-sm text-center">
                            Aplicando clonación vocal y auto-mezclando niveles en la consola...
                        </p>
                    </div>
                </div>
            )}
        </div>
    );
}
