'use client';

import { useDAWStore } from '@/store/useDAWStore';
import { Slider } from '@/components/ui/Slider';
import { VUMeter } from './VUMeter';

interface FaderProps {
    id: string;
}

export function Fader({ id }: FaderProps) {
    const fader = useDAWStore((state) => state.faders.find((f) => f.id === id));
    const setFaderValue = useDAWStore((state) => state.setFaderValue);
    const toggleSolo = useDAWStore((state) => state.toggleSolo);
    const toggleMute = useDAWStore((state) => state.toggleMute);

    if (!fader) return null;

    return (
        <div className="flex flex-col items-center w-16 group">

            {/* Top Section: Buttons & Small Screen */}
            <div className="flex flex-col gap-2 items-center mb-6 w-full px-1">

                {/* Mini Routing/Pan Screen simulation */}
                <div className="w-full h-8 bg-[#0a1015] border border-cyan-glow/20 rounded-sm flex items-center justify-center shadow-inner mb-2 overflow-hidden relative">
                    <div className="absolute inset-0 bg-cyan-glow/5" />
                    <span className="text-[9px] font-mono text-cyan-glow tracking-tighter mix-blend-screen">{fader.pan === 0 ? 'C' : fader.pan}</span>
                </div>

                <button
                    onClick={() => toggleSolo(id)}
                    className={`w-full h-6 rounded-sm text-[9px] font-bold border transition-colors shadow-sm ${fader.isSoloed ? 'bg-[#ffaa00] border-[#ffaa00] text-black shadow-[0_0_10px_rgba(255,170,0,0.6)]' : 'bg-[#1a1a1a] border-[#333] text-silver-dark hover:bg-[#2a2a2a]'}`}
                >
                    SOLO
                </button>
                <button
                    onClick={() => toggleMute(id)}
                    className={`w-full h-6 rounded-sm text-[9px] font-bold border transition-colors shadow-sm ${fader.isMuted ? 'bg-red-600 border-red-600 text-black shadow-[0_0_10px_rgba(255,0,0,0.6)]' : 'bg-[#1a1a1a] border-[#333] text-silver-dark hover:bg-[#2a2a2a]'}`}
                >
                    MUTE
                </button>
            </div>

            {/* Main Fader Track Area */}
            <div className="relative h-[300px] w-full bg-[#111] border-x border-[#222] flex shadow-inner group-hover:bg-[#141414] transition-colors rounded-t-sm">

                {/* Scale Markings Container */}
                <div className="absolute left-1 inset-y-0 w-2 flex flex-col justify-between py-4 pointer-events-none z-0">
                    {[10, 5, 0, -5, -10, -20, -30, -60].map((val, i) => (
                        <div key={i} className="w-full border-t border-silver-dark/40 relative">
                            {(val === 0 || val === 10 || val === -60) && (
                                <span className={`absolute left-3 -top-2 text-[7px] font-mono ${(val === 0) ? 'text-primary' : 'text-silver-dark'}`}>
                                    {val === -60 ? '-∞' : val}
                                </span>
                            )}
                        </div>
                    ))}
                </div>

                {/* LED VU Meter integrated into fader track */}
                <div className="absolute right-1 top-4 bottom-4 w-1.5 opacity-80 pointer-events-none z-0">
                    <VUMeter level={fader.isMuted ? 0 : Math.floor(fader.value * 1.27)} className="h-full w-full rounded-sm" />
                </div>

                {/* Functional Slider Track */}
                <div className="absolute inset-0 flex justify-center z-10 w-full">
                    {/* The physical slit */}
                    <div className="absolute top-4 bottom-4 w-1.5 bg-black rounded-full shadow-[inset_0_2px_5px_rgba(0,0,0,0.8)]" />

                    <Slider
                        orientation="vertical"
                        min={0}
                        max={100}
                        value={fader.value}
                        onChange={(e) => setFaderValue(id, parseInt(e.target.value))}
                        className="h-full w-full cursor-grab active:cursor-grabbing transition-all duration-700 ease-in-out opacity-0 z-20"
                        style={{ WebkitAppearance: 'slider-vertical' } as React.CSSProperties}
                    />

                    {/* Simulated 3D Fader Cap that tracks the input */}
                    <div
                        className="absolute w-8 h-12 bg-gradient-to-b from-[#b3b3b3] via-[#e6e6e6] to-[#808080] border border-[#d9d9d9] shadow-[0_5px_10px_rgba(0,0,0,0.6),_inset_0_1px_1px_rgba(255,255,255,0.8)] rounded-sm pointer-events-none z-10 transition-all duration-700 ease-in-out flex flex-col items-center justify-center transform-gpu"
                        style={{ bottom: `calc(${fader.value}% - 24px)` }}
                    >
                        {/* Grip lines */}
                        <div className="w-6 h-0.5 bg-black/40 mb-1" />
                        <div className="w-6 h-1 bg-gradient-to-r from-[#e6e6e6] via-white to-[#e6e6e6]" />
                        <div className="w-6 h-0.5 bg-black/40 mt-1" />
                    </div>
                </div>

            </div>

            {/* Scribble Strip (Channel Name) */}
            <div className="w-full h-10 bg-[#e6e6e6] mt-1 rounded-b-md flex items-center justify-center shadow-[inset_0_2px_4px_rgba(0,0,0,0.1)] border-b border-[#999]">
                <span className="font-mono text-xs font-bold text-black opacity-80">{fader.label}</span>
            </div>

        </div>
    );
}
